.blog {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-bottom: 40px;
  box-sizing: border-box;
}

.blog_title {
  font-family: 'Gramatika-Regular', sans-serif;
  font-size: 72px;
  line-height: 110%;
  color: #121111;
  margin-bottom: 80px;
  width: 100%;

  &--in-article {
    margin-bottom: 50px;
  }
}

.blog_item {
  flex-basis: 48%;
  flex-shrink: 0;
  text-decoration: none;

  &:hover {
    cursor: pointer;

    & .blog_description {
      color: #6ADF91;
    }
  }
}

.blog_img {
  height: 338px;
  width: 100%;
  background: #F3F3F3;
  border-radius: 12px;
  margin-bottom: 25px;
}

.blog_description {
  font-family: 'Gramatika-Regular', sans-serif;
  font-size: 28px;
  line-height: 110%;
  color: #121111;
}

@media(max-width: 1190px) {
  .blog {
    flex-direction: column;
  }

  .blog_title {
    font-size: 54px;
    line-height: 110%;

    &--in-article {
      margin-bottom: 80px;
    }
  }

  .blog_item {
    width: 100%;
    margin-bottom: 20px;
  }
}

@media(max-width: 767px) {
 .blog_title {
  font-size: 38px;
  line-height: 110%;
  margin-bottom: 40px;
 }

 .blog_img  {
    margin-bottom: 15px;
  }

  .blog_description {
    font-size: 20px;
  }
}
